#first {
  background-color: #f34343;
  height: 100vh;
}
#second {
  background-color: #43f343;
}

.box55 {
  width: 150px;
  height: 150px;
  background-color: yellow;

  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  left: 50%;
  top: calc(50vh - 100px);
  color: white;
  z-index: 66;
}

.intro-label {
  margin-bottom: 5px;
  font: 500 14px/22px Roboto Mono, monospace;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  color: #43495c;
}

.intro-label-black {
  margin-bottom: 5px;
  font: 500 14px/22px Roboto Mono, monospace;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  color: #fff;
}

.intro-text h3 {
  font-size: 56px;
  line-height: 64px;
  font-weight: 300;
  color: $black-color;
  margin-top: 16px;
  position: relative;
  top: -4px;
  letter-spacing: -0.01em;
}

.intro-text h2 {
  margin-bottom: 1.5rem;
  color: $black-color;
}

.intro-text p {
  font-size: 1.125rem;
  line-height: 1.7;
  font-weight: 400;
  color: #43495c;
  letter-spacing: 0.01em;
  margin: 0.4em 0 1.5rem;
}

.intro-text-black p {
  font-size: 1.125rem;
  line-height: 1.7;
  font-weight: 400;
  color: rgb(226, 226, 226);
  letter-spacing: 0.01em;
  margin: 0.4em 0 1.5rem;
}

.intro-text-red p {
  font-size: 1.125rem;
  line-height: 1.7;
  font-weight: 400;
  color: #fff;
  letter-spacing: 0.01em;
  margin: 0.4em 0 1.5rem;
}

// .cover {

// }

.cover img {
  height: 40vh;
}

.cta button {
  border: 2px solid white;
  background-color: transparent;
  font-size: 1.3rem;
  font-weight: 600;
  margin: 1rem 0.5rem 0rem 0;
  padding: 0.3rem 1.2rem !important;
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  white-space: nowrap;
  padding-bottom: 2px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.cta button.active {
  background: #c82736;
}
.cta button:hover {
  background-color: white;
  color: black;
}
.cta button:focus {
  outline: none;
  background-color: white;
  color: black;
}

.cta a {
  color: inherit;
}

.cta a:link {
  text-decoration: none;
}

.cta a:visited {
  text-decoration: none;
}

.cta a:hover {
  color: black;
  text-decoration: none;
}

.cta a:active {
  color: black;
  text-decoration: none;
}

.abstand li {
  list-style-type: none;
  line-height: 1.5;

  font-size: 1.1rem;
}

.abstand li span {
  position: relative;
  left: 0px;
}

/* Media Queries */
@media (max-width: 600px) {
  .imgs {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 767px) {
  .demo-text .text {
    font-size: 4.5rem;
    line-height: 1;
    font-weight: 900;
    color: white;
  }

  .box55 {
    width: 25px;
    height: 25px;
    background-color: yellow;

    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    top: calc(50vh - 100px);
    color: white;
  }
}
