.boxButton {
  padding: 0;
  background: red;
}

.box31 {
  flex: 1 3 50%;
  padding: 50px;
}
.box32 {
  flex: 4 1 50%;
  clip-path: circle(24% at center);
  background: rgba(255, 255, 255, 0.5);
  transition: all 1s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
}
.box35 {
  padding: 0;
  flex: 1 0 50%;

  padding: 35px;
  background: #005ca7;
}

.box36 {
  padding: 0;
  flex: 1 0 50%;
  padding: 50px;
  background: #e50041;
}

.box37 {
  padding: 0;
  flex: 1 0 100%;
  padding: 50px;
  background: green;
}

.box38 {
  padding: 0;
  flex: 1 0 100%;
  padding: 10px;
