  // background: rgba(255, 255, 255, 0.5);

  transition: all 1s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  overflow: hidden;
  img {
    object-fit: cover;
  }
}

.box51 {
  background-color: rgba(255, 255, 255, 0.5);
  flex: 1 3 50%;
  padding: 50px;
  color: $black-color;
  p {
