@media all and (max-width: 1300px) {
  .box {
    // font-size: 2rem;
    text-align: left;
    // text-shadow: 4px 4px 0 rgba(0, 0, 0, 0.1);
    display: flex;
    // text-transform: uppercase;
  }
  //   /* Reorder items */
  .container > * {
    order: 999;
  }
  .box1 {
    flex: 1 2 50%;
    order: 3;
  }

  .box2 {
    flex: 5 1 50%;
    order: 1;
    .avatar {
      // background: red;
      // background: rgba(255, 255, 255, 0.4);
      clip-path: circle(42% at center);
      // transition: all 1s ease-in-out;
      img {
        width: 80%;
      }
    }
    clip-path: circle(28% at center);
    background: rgba(255, 255, 255, 0.5);
    transition: all 1s ease-in-out;
  }

  .box3 {
    order: 2;
    flex: 1 0 40%;
    h2 {
      font-size: 1.5rem;
    }
  }
  .box4 {
    order: 4;
    flex: 1 2 100%;
  }
  .box5 {
    order: 5;
    flex: 1 2 100%;
    p {
      font-size: 1.3rem;
    }
  }

  .box6 {
    order: 6;
    flex: 1 2 100%;
  }
  .box9 {
    flex: 0 0 100%;
    display: flex;
    justify-content: center;
    min-height: 100%;
    flex-shrink: 0;
    // align-content: flex-start;
  }

  .box52 {
    flex: 4 1 42%;
    // background: rgba(255, 255, 255, 0.5);

    transition: all 1s ease-in-out;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    overflow: hidden;
    img {
      object-fit: cover;
    }
  }

  .box51 {
    flex: 1 3 100%;
    padding: 50px 20px;
    p {
      color: $black-color;
    }
    a {
      color: $black-color;
    }
  }

  .box56 {
    padding: 0;
    flex: 1 0 100%;
    padding: 50px;
    background-color: rgba(4, 12, 38, 0.95);
  }
}

