@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;500;700&display=swap");
@import "config";
@import "color";
@import "header";
@import "test";
@import "slider";
@import "marquee";
@import "timer";
@import "animations";
@import "panel";
@import "box";
@import "media";
@import "avatar";
@import "textani";
@import "portfolio";
@import "gallery";

body {
  font-family: "Roboto", sans-serif;
  height: 100%;
  line-height: 1.5;

  background-color: $black-color;
  overflow-x: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

h1 {
  font-size: 2.875rem;
  line-height: 1.15;
  font-weight: 900;
  letter-spacing: -0.012em;
}

h2 {
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.175;
  font-weight: 700;
  letter-spacing: -0.012em;
}
h3 {
  font-size: 1.75rem;
  line-height: 1.45;
  font-weight: 400;
  letter-spacing: -0.005em;
}
h4 {
  font-size: 24px;
  font-weight: 500;
  // text-transform: uppercase;
}
h5 {
  font-size: 2rem;
  font-weight: 800;
}
h6 {
  font-size: 18px;
  font-weight: 600;
}
hr.solid {
  border-top: 3px solid #bbb;
}
