.wrapper1 {
  max-width: 100vw; /* iOS braucht das */
  position: relative;
  width: 150%;
  height: 80px;
  left: 50%;
  top: 40%;
  background: $black-color;
  overflow: hidden;
}
.wrapper1 .boxT {
  position: absolute;
  width: 250px;
  height: 50px;
  padding-bottom: 20px;
  background-color: $black-color;
  color: white;
  font-size: 40px;
  font-weight: 600;
  line-height: 50px;
  text-align: center;
  text-transform: uppercase;
}
.wrapper1 .boxes {
  position: absolute;
  left: -250px;
}
