.containerSlider {
  width: 80%;
  height: 100vh;
  display: flex;
  margin: auto;
  margin-top: 100px;
  justify-content: center;
  align-items: flex-start;
  // background: yellow;
}

.slide {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 40px;
  flex-shrink: 0;
}

.slide-image {
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  transition: all 0.5;
}

.glide__slide--active .slide-image {
  height: 100%;
}

.slide-image img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}

.slide p {
  color: white;
  font-size: 14px;
}

// .glide__slide img {
//   width: 100%;
// }

.glide__slide {
  // filter: blur(3px);
  opacity: 0.9;
  transition: 0.3s linear;
  transform: scale(0.8);
}

.glide__slide--active {
  opacity: 1;
  filter: none;
  transform: scale(1);
}

.glide__slides {
  overflow: visible;
  color: white;
}

.glide__arrow {
  font-size: 1.5rem;
  background-color: rgba(253, 241, 0, 0.5) !important;
  top: 40%;
  border-radius: 0 !important;
}

.glide__arrow:hover {
  background-color: rgba(253, 241, 0, 1) !important;
  .fa {
    color: black;
  }
}

.glide__arrow:active {
  background-color: $secondary-color !important;
}

.boxSlider {
  background-color: rgba(4, 12, 38, 1);

  padding: 5px;
  h2 {
    color: white;
    font-size: 1.8rem;
  }
  h5 {
    color: white;
    font-size: 1.3rem;
    font-weight: 500;
  }
}

/* Bottom left text */
.bottom-left {
  position: absolute;
  bottom: 100px;
  left: 0px;
  padding-left: 30px;
  padding-right: 20px;
}

.curve {
  position: absolute;
  bottom: 55px;
  left: 0px;
  padding-left: 30px;
  padding-right: 20px;
}

/* Top left text */
.top-left {
  font: 500 14px/22px Roboto Mono, monospace;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  position: absolute;
  bottom: 35px;
  right: 0;
  padding-left: 10px;
  padding-right: 15px;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
  // transform: rotate(-90deg);
}

/* Top right text */
.top-right {
  position: absolute;
  top: 5%;
  right: 0;
}

/* Bottom right text */
.bottom-right {
  position: absolute;
  bottom: 5%;
  right: 0;
}

/* Centered text */
.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 5px 10px;
  transform: translate(-50%, -50%);
  background-color: rgba(4, 12, 38, 0.6);
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12),
    0 3px 5px -1px rgba(0, 0, 0, 0.2);
  transition: all 0.4s ease-out;
}

