@media all and (max-width: 1300px) {
  .containerSlider {
    width: 100%;
    height: 100vh;
    display: flex;
    margin-top: 100px;
    justify-content: center;
    align-content: center;
  }
}

@media only screen and (max-width: 768px) {
  .containerSlider {
    width: 100%;
    height: 100vh;
    display: flex;
    margin: auto;
    margin-top: 60px;
    justify-content: center;
    align-items: flex-start;
    img {
      object-fit: cover;
      object-position: 50% 0;
      min-height: 600px;
    }

    // background: yellow;
  }

  .glide__arrow {
    background-color: rgba(253, 241, 0, 0.5) !important;
    .fa {
      display: inline-block;
      font: normal normal normal 18px/1 FontAwesome;
      font-size: 1.5rem;
      text-rendering: auto;
      color: white;
    }
  }

  .glide__arrow:hover {
    background-color: rgba(253, 241, 0, 1) !important;
    .fa {
      color: black;
    }
  }

  .glide__arrow:active {
    background-color: rgb(232, 0, 253) !important;
  }

  .bottom-left {
    h2 {
      color: white;
      font-size: 1.3rem;
    }
    position: absolute;
    bottom: 110px;
    left: 0px;
    padding-left: 30px;
    padding-right: 20px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
  }

  .curve {
    position: absolute;
    bottom: 73px;
    left: 0px;
    padding-left: 30px;
    padding-right: 20px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 5px;
    h5 {
      color: white;
      font-size: 1rem;
      font-weight: 500;
    }
  }
  /* Centered text */
  .centered {
    h2 {
      font-size: 2.2rem;
    }
    transform: translate(-50%, -50px);
  }
}
